* {
  outline: none;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

html,body,h1,h2,h3,h4,h5,h6 {
  margin: 0;
}

a[href] {
  text-decoration: none;
}

a[href].link:hover {
  color: #d1d1d1;
}

ul {
  margin: 0;
  padding: 0;
}

img:not([src$='star.svg']):not([class='footer__mobile-link-icon']) {
  display: block;
}

.pos--rel {
  position: relative;
}

.pos--abs {
  position: absolute;
}

.pos--fixed {
  position: fixed;
}

.d--none {
  display: none;
}

.d--block {
  display: block;
}

.d--ib {
  display: inline-block;
}

.d--flex {
  display: flex;
}

.d--if {
  display: inline-flex;
}

.d--grid {
  display: grid;
}

.full-width {
  width: 100%;
}

.bg--white {
  background: #ffffff;
}

.bg--black {
  background: #171717;
}

.bg--palewhite {
  background: #eeeeee;
}

.bg--paleblack {
  background: #1d1d1d;
}

.bg--green {
  background: #75c74e;
}

.flex--1 {
  flex: 1;
}

.ai--center {
  align-items: center;
}

.jc--between {
  justify-content: space-between;
}

.jc--around {
  justify-content: space-around;
}

.jc--center {
  justify-content: center;
}

.flex--column {
  flex-direction: column;
}

.flex--wrap {
  flex-wrap: wrap;
}

.no-border {
  border: none;
}

.bordered {
  border-width: 1px;
  border-style: solid;
}

.bordered--top {
  border-top-width: 1px;
  border-top-style: solid;
}

.bordered--bottom {
  border-bottom-width: 1px;
  border-bottom-style: solid;
}

.brdr--clear {
  border-color: rgba(255, 255, 255, 0.1);
}

.text--bold {
  font-weight: 700;
  font-size: 15px;
}

.text--right {
  text-align: right;
}

.text--center {
  text-align: center;
}

.text--gray {
  color: #919191;
}

.text--white {
  color: #fff;
}

.text--palewhite {
  color: #eeeeee;
}

.text--palewhite:hover {
  color: #919191;
}

.ribbon--blue {
  background: #1d6baf;
}

.ribbon--black {
  background: #000000;
}

.cur--pointer {
  cursor: pointer;
}

.center {
  max-width: 90%;
  margin: auto;
}

.section-header {
  padding-bottom: 1.5rem;
}

.star {
  width: 20px;
}

.button {
  width: 120px;
  padding: 0.8rem;
  border-radius: 0.3rem;
}

.ribbon {
  top: 1vw;
  left: -3.4vw;
  width: 12.5vw;
  font-size: 1.7vw;
  padding: 0.2rem 0;
  z-index: 1;
  transform: rotate(-40deg);
}

.card {
  width: 21%;
}

.card:nth-child(8),.card:nth-child(7),.card:nth-child(6),.card:nth-child(5) {
  margin-top: 1rem;
}

.card .image-container {
  border: 6px solid #ffffff;
  border-radius: 0.3rem;
  overflow: hidden;
}

.card .image-container:hover {
  border-color: #75c74e;
}

.card .image-container:hover .overlay {
  opacity: 1;
}

.card .image-container:hover .overlay .button {
  transform: translateY(0);
}

.card .overlay {
  top: 0;
  left: 0;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  transition: opacity 250ms ease-in-out;
}

.card .button {
  transform: translateY(30%);
  transition: transform 250ms ease-in-out;
}

.card .info {
  padding: 10% 0;
}

.card .title {
  margin: 0.2rem 0 0.2rem 0.2rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.card .year {
  margin: 0.2rem 0 0.2rem 0.2rem;
}

button.button--green,input[type='submit'].button--green {
  background: #75c74e;
  cursor: pointer;
}

button.button--green:hover,input[type='submit'].button--green:hover {
  background: #5cac36;
}

button.button--orange {
  background: #cf9e2f;
}

button.button--orange:hover {
  background: #a57e26;
}

.header {
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
}

.header__search {
  width: 250px;
  background: transparent;
  margin-right: 1rem;
  padding: 0.45rem 1rem;
  border: 2px solid #292929;
  border-radius: 5rem;
}

.header__link {
  padding: 1.5rem 1rem;
}

.header__search-dropdown {
  top: 101%;
  left: 0;
  display: none;
  background: #111111;
  padding: 1rem 0;
}

.header__mobile-search {
  background: #313131;
  padding: 0.45rem;
  border-radius: 0.2rem 0 0 0.2rem;
}

.header__submit {
  border-radius: 0 0.2rem 0.2rem 0;
}

.header__search-options {
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem 0;
  margin-top: 0.7rem;
}

.header__option {
  width: 30%;
}

.header__option-select {
  background: #242424;
  color: #eeeeee;
  padding: 0.45rem;
  margin-top: 0.2rem;
}

.hero {
  background-image: url("https://res.cloudinary.com/dt9ntq5vr/image/upload/w_1146,h_573,e_blur:615/v1592876947/yts-clone/posters/Screen-Shot-2019-12-19-at-12.24.00-PM_xqbknp.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 1;
}

.hero:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(to bottom, rgba(29, 29, 29, 0.65) 0, #1d1d1d 100%);
  z-index: -1;
}

.hero__head {
  padding-top: 5.5rem;
}

.hero__title {
  font-size: 3vw;
  text-shadow: 1px 2px 5px #111;
}

.hero__subtitle {
  width: 58%;
  margin: auto;
  font-size: 1.25vw;
  padding: 1.5rem 0;
}

.hero__popular-header {
  padding-bottom: 1.5rem;
}

.hero__cards {
  padding: 1.5rem;
}

.movies {
  background: #101010;
  padding: 3rem 0;
}

.movies__main {
  align-content: space-between;
  padding: 0 1.5rem;
}

.upcoming {
  padding: 3rem 0;
}

.upcoming__main {
  padding: 0 1.5rem 0 1.5rem;
}

.footer__bar {
  padding: 1.5rem 0;
}

.footer__list-item {
  margin: 0 1rem;
}

.footer__mobile-link-block {
  width: 33.3%;
  margin: 1.5rem 0;
}

.footer__mobile-link-icon {
  width: 3vw;
}

.footer__mobile-link-label {
  font-size: 2.2vw;
}

.form {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 20;
}

.form__wrapper {
  width: 550px;
  margin: 5vw auto 0 auto;
  box-shadow: 0 8px 5px -4px rgba(0, 0, 0, 0.5);
}

.form__tabs {
  border-radius: 0.4rem 0.4rem 0 0;
}

.form__login-tab,.form__register-tab {
  padding: 1.5rem 0;
  font-size: 1.1rem;
}

.form__login-tab {
  border-top-left-radius: 0.4rem;
}

.form__register-tab {
  border-top-right-radius: 0.4rem;
}

.form__container {
  padding: 1.5rem 0;
  border-radius: 0 0 0.4rem 0.4rem;
}

.form__login,.form__register {
  width: 90%;
  margin: auto;
  display: none;
}

.form__field {
  border: 1px solid #cfcfcf;
  margin-top: 1.5rem;
  padding: 0 1rem;
  border-radius: 0.2rem;
}

.form__icon-container {
  margin-right: 1rem;
}

.form__icon {
  width: 20px;
}

.form__input {
  font-size: 0.95rem;
  padding: 1rem 0;
}

.form__input::placeholder {
  color: #aeaeae;
}

.form__submit {
  margin-top: 1.5rem;
  padding: 1rem 0;
  border-radius: 0.2rem;
}

.header__search-dropdown.dropped {
  display: block;
}

.form.shown {
  display: block;
}

label.active-tab {
  background: #ffffff;
  color: #75c74e;
  font-weight: 700;
  cursor: default;
}

form[class^='form__'].active-form {
  display: block;
}

@media (max-width: 767px) {
  .card {
    width: 45%;
  }

  .card .overlay {
    display: none;
  }

  .card .ribbon {
    top: 1.7vw;
    left: -4.5vw;
    width: 18vw;
    font-size: 2.2vw;
  }

  .card:not(:nth-child(1)):not(:nth-child(2)) {
    margin-top: 1rem;
  }
}

@media (max-width: 540px) {
  .section-header > * {
    font-size: 3vw;
  }
}

@media (max-width: 977px) {
  .header__right {
    display: none;
  }

  .header__logo-image {
    width: 14.5vw;
  }

  .header__mobile-icon {
    width: 3vw;
  }

  .header__mobile-right {
    display: block;
  }

  .header__mobile-nav {
    display: flex;
  }

  .header__mobile-link {
    display: block;
    padding: 1rem;
  }
}

@media (max-width: 540px) {
  .header__logo-image {
    width: 5rem;
  }

  .header__mobile-icon {
    width: 1rem;
  }

  .header__mobile-link {
    padding: 1rem 0.7rem;
  }
}

@media (max-width: 767px) {
  .hero__head {
    display: none;
  }

  .hero__popular {
    padding-top: 5.5rem;
  }
}

@media (min-width: 768px) {
  .footer__mobile-block {
    display: none;
  }
}

@media (max-width: 767px) {
  .footer__list {
    display: none;
  }
}

@media (max-width: 540px) {
  .footer__mobile-link-icon {
    width: 1rem;
  }

  .footer__mobile-link-label {
    font-size: 0.7em;
  }
}